import React ,{useState} from "react";
import "./Codes.css"
import Flickity from "flickity"

export default function Codes(prop){
    const [viewWidth , setViewWidth] = useState(window.innerWidth)

    const tabShow = viewWidth > 800 ? "js%2Cresult" : "result"

    function handleResize(){
        setViewWidth(()=> window.innerWidth)
    }

    window.addEventListener("resize",handleResize)

    return(
        <div className="codeBoxes gallery-cell" >
            <h2 className="codeTitles" >{prop.iframes.title}</h2>
            <iframe  
                className="codepenData"
                title="React Fetch Random Card" 
                src={prop.iframes.iframe+"?default-tab="+tabShow}
                frameBorder="no" 
                loading="lazy" 
                allowtransparency="true" 
                allowFullScreen={true}
            >
                See the Pen <a href={prop.iframes.codepensite}>
                React Fetch Random Card</a> by Crisandro Basoy (<a href="https://codepen.io/crisandrobasoy">@crisandrobasoy</a>)
                on <a href="https://codepen.io">CodePen</a>.
            </iframe>
            <span className="messages">{prop.iframes.message}</span>
        </div>
    )
}