import React,{useEffect} from "react"
import VanillaTilt from "vanilla-tilt"
import "./Card.css"
import monstercard from "./Img/monster-normal.png"
import star from "./Img/star.png"
import myPicture from "./Img/animation.jpg"
import monsterType from "./Img/wind.png"

export default function Card(){
    useEffect(()=> {
        VanillaTilt.init(document.getElementById("monstercard"), {
            max: 25,
            speed: 400,
            glare: true,
            "max-glare": 0.8,
            perspective: 2000
        })
    },[])
    

    return(
        <>
            <div id="monstercard" className="card">
                <div className="front">
                    <img className="cardFront" src={monstercard} alt="" draggable="false" />
                    <div className="title--place">
                        <h1 id="cardTitle">The Creator</h1>
                        <img id="cardType" src={monsterType} alt="" draggable="false"/>
                    </div>
                    <div className="star--place">
                        <img id="starLevel" src={star} alt="" draggable="false"/>
                        <img id="starLevel2" src={star} alt="" draggable="false"/>
                        <img id="starLevel3" src={star} alt="" draggable="false"/>
                        <img id="starLevel4" src={star} alt="" draggable="false"/>
                        <img id="starLevel5" src={star} alt="" draggable="false"/>
                        <img id="starLevel6" src={star} alt="" draggable="false"/>
                        <img id="starLevel7" src={star} alt="" draggable="false"/>
                        <img id="starLevel8" src={star} alt="" draggable="false"/>
                        <img id="starLevel9" src={star} alt="" draggable="false"/>
                        <img id="starLevel10" src={star} alt="" draggable="false"/>
                        <img id="starLevel11" src={star} alt="" draggable="false"/>
                        <img id="starLevel12" src={star} alt="" draggable="false"/>
                    </div>
                    <div className="mypic">
                        <img src={myPicture} alt=""/>
                    </div>
                    <div className="desc--place">
                        <h4 id="monsterType">[Developer]</h4>
                        <p id="monsterDescription">A guy who loves playing around with javascripts and CSS. He also use Illustrator to create vector logos or Photoshop to Edit and manipulate photos for certain projects or for fun. Enjoy browsing some of his works here.</p>
                        <div className="att-def">
                            <p id="monsterAtk">?</p>
                            <p id="monsterDef">?</p>
                        </div>
                        
                    </div>
                </div>
                <img id="back" src="Img/SlifertheSkyDragon-GBI-AE-Back.webp" alt="" draggable="false"/>
            </div>
        </>
    )
}