import React,{useState} from "react";
import "./Navbar.css"
import Logo from "./Img/MyLogo.png"
import menuIcon from "./Img/hamburger.png"
import XmenuIcon from "./Img/x-bar.png"

export default function NavBar(){
    const [viewWidth , setViewWidth] = useState(window.innerWidth)
    const [showMenu , setShowMenu] = useState(viewWidth < 800 ? true : false)
    const [showLinks , setShowLinks] = useState(false)

    function handleResize(){
        setViewWidth(()=> window.innerWidth)
        viewWidth < 800 ? setShowMenu(true) : setShowMenu(false)
    }
    
    function handleShowMenu(){
        setShowLinks(prevValue => !prevValue)
    }

    window.addEventListener("resize",handleResize)

    window.addEventListener("scroll", function(){
        let navbar = this.document.getElementById("nav")
        let pageTitle = this.document.getElementById("pagetitle")
        let myname = this.document.getElementById("Myname")
        let navlinks = document.getElementsByClassName("links")[0]
        
        if(viewWidth > 800){
            if (document.body.scrollTop > 750 || document.documentElement.scrollTop > 750) {
                navbar.style.height = "50px"
                myname.style.display = " block "
                navlinks.style.display = "flex"
            }else{
                navbar.style.height = "100px"
                myname.style.display = " none "
                navlinks.style.display = "none"
            }
        }else{
            if (document.body.scrollTop > 750 || document.documentElement.scrollTop > 750) {
                navbar.style.height = "50px"
                myname.style.display = " block "
            }else{
                navbar.style.height = "100px"
                myname.style.display = " none "
            }
        }
        
    })

    return (
        <div className="navigator" id="nav">
            <a href="#TopHolder" className="forTitle">
                <div id="Myname">Crisandro Basoy</div>
                <img className="pageTitle" id="pagetitle" />
            </a>
            
            
            {viewWidth > 800 ? 
            <div className="links">
                <a href="https://github.com/Crisandro" target="_blank" rel="noopener noreferrer" className="testing github">Github</a>
                <a href="https://codepen.io/crisandrobasoy" target="_blank" rel="noopener noreferrer" className="testing codepen">Codepen</a>
                <a href="#contactme" className="testing codepen" >Contact Me</a>
            </div>
            :
            showLinks && <div className="mlinks">
                <a href="https://github.com/Crisandro" target="_blank" rel="noopener noreferrer" className="mtesting" onClick={handleShowMenu}>GITHUB</a>
                <a href="https://codepen.io/crisandrobasoy" target="_blank" rel="noopener noreferrer" className="mtesting" onClick={handleShowMenu}>CODEPEN</a>
                <a href="#contactme" className="mtesting" onClick={handleShowMenu}>CONTACT ME</a>
            </div>}
            {showMenu && <img className="menuButton" src={showLinks ? XmenuIcon : menuIcon} onClick={handleShowMenu} /> }
        </div>
    )
}