import React,{useEffect} from "react";
import "./StaticBackGround.css"
import Card from "./Card"
import GithubIcon from "./Img/287725_github_icon.ico"
import CodePenIcon from "./Img/1591833_codepen_icon.ico"
import MessageIcon from "./Img/4550857_email_gmail_mail_sending_yahoo_icon.ico"
import ResumeDocx from "./Img/CrisandroBasoyResume2023.docx"
import Typed from 'typed.js'

export default function StaticBackGround(){

    useEffect(() => {
        const Strings = [
            ' I am a Creative Developer ',
            ' I am a Content Moderator ',
            ' I am a Designer ',
            ' I Like to Learn New Things... '
        ]
        
        const options = {
            strings: Strings,
            typeSpeed: 50,
            backSpeed: 50,
            backDelay: 2000,
            startDelay: 2000,
            loop: true, 
        }
        
        // elRef refers to the <span> rendered below
        const typed = new Typed('.typed', options)
        
        return () => {
          // Make sure to destroy Typed instance during cleanup
          // to prevent memory leaks
          typed.current.destroy()
        }
    }, [])
    
    

    

    return(
        <div className="staticBG">
            <Card />
            <span className="stickBackGroundName">Crisandro Basoy</span>
            <span className="stickBackGroundProf">
                <span className="typed"></span>
            </span>
            <div className="linksIcon">
                <a href={ResumeDocx} download="Crisandro's Resume" className="myResume">
                    Download My Resume
                </a>
                <a href="https://github.com/Crisandro" target="_blank" rel="noopener noreferrer">
                    <img src={GithubIcon} />
                </a>
                <a href="https://codepen.io/crisandrobasoy" target="_blank" rel="noopener noreferrer">
                    <img src={CodePenIcon} />
                </a>
                <a href="#contactme" >
                    <img src={MessageIcon} />
                </a>
            </div>
        </div>
    )
}